.title {
  margin-bottom: 10px;
}

.list {
  display: flex;
  margin-bottom: 20px;
  list-style: none;
  /*text-decoration: none;*/
}

.list li:first-child {
  margin-right: 15px;
}

.link {
  font-size: 20px;
  text-transform: uppercase;
  color: #04139b;
  transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.link:hover,
.link:focus {
  color: #3498db;
}
